.node{
    width: 220px;
    height: 70px;
    border-radius: 4px;
    margin-left: 10px;
}
.node:nth-child(1){
    margin-left: 0;
}
.node_up{
    box-shadow: 0px 1px 4px 0px green;
}
.node_up:hover{
    cursor: pointer;
}
.node_down{
    box-shadow: 0px 1px 4px 0px red;
}
.node_down:hover{
    cursor: pointer;
    background: red;
}
.node_title{
    padding:5px;
    font-size: 16px;
    margin: 0;
    text-align: center;
}


.node_stats{
    width: 90%;
    display: flex;
    margin: 0 auto;
    
}
.node_stats h1{
    width: 100px;
    font-size: 16px;
    margin-left: 5px;
}
.node_stats h1:nth-child(1){
    margin-left: 0;
}