

@media only screen and (max-width: 1300px) {
    .query_txt_container{
        
        display: block;
        margin: 0 auto;
        margin-top: -10px;
    }
}
@media only screen and (min-width: 1300px) {
    .query_txt_container{
        display: block;
        margin: 0 auto;
        margin-top: -60px;
    }
}

.query_txt_container{
    width: 50%;
    min-width: 500px;
    max-width: 600px;
}
#query_txt_container h1{
    font-size: 16px;
}
#query_txt{
    font-weight: 500;
    font-size: 16px;
    width: 100%;
    height: 80px;
    resize: none;
    padding: 10px;
    border: 1px solid #bbb;
    border-radius: 3px;
}

#query_btn{
    border: 1px solid #5f5fb3;
    background: #474787;
}

#query_btn:hover{
    cursor: pointer;
    background: #5f5fb3;
}

#query_results_title{
    font-size:20px;
    margin-top: 30px;
}