#header{
    width: 100%;
    height: 55px;
    border-bottom: 1px solid #ccc;
    background: #2c2c54;
    position: fixed;
    z-index: 1;
    color: white;
}
#header_container{
    height: 100%;
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
}
#header_container_left{
    display: flex;
}
#logo{
    font-size: 25px;
    padding: 5px;
    background: #141426;
}
#logo:hover{
    cursor: pointer;
}
#menu{
    display: flex;
    align-items: center;
    margin-left: 40px;
}
.menu_item:nth-child(1){
    margin-left: 0;
}
.menu_item{
    display: flex;
    align-items: center;
    margin-left: 20px;
    padding: 0 5px;
}
.menu_item:hover{
    cursor: pointer;
    background: rgba(0,0,0,0.3);
   
}
.menu_item img{
    width: 25px;
    height: 25px;
}
.menu_item h1{
    font-size: 16px;
    margin-left: 5px;
}


#add_node{
    border: 1px solid #5f5fb3;
    background: #474787;
}

#add_node:hover{
    cursor: pointer;
    background: #5f5fb3;
}