

*:focus{
    outline: none;
    
}
button{
    font-size: 14px;
    font-weight: 500;
    width: 100px;
    height: 30px;
    border-radius: 2px;
    color: white;
}
select:hover{
    cursor: pointer;
}

.folder{
    display: inline;
    margin-left: 20px;
    text-decoration: underline;
}
.folder:nth-of-type(1){
    margin-left: 0;
}
.folder:hover{
    cursor: pointer;
}

/* split this */
.modal-position{
    position:fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0,0.6);
    z-index: 100;
}
.modal_title{
    color: black;
}
#add_node_modal{
    width: 300px;
    height: 200px;
    background: white;
    display:block;
    margin: 0 auto;
    margin-top: 30px;
    border: 1px solid #ccc;
}
.modal_container{
    padding: 10px;
    width: 90%;
    display: block;
    margin: 0 auto;
}
#add_node_txt{
    width: 250px;
    height: 30px;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding-left: 10px;
}
#add_node_txt:focus{
    outline: none;
}
#add_node_btn{
    width: 100px;
    height: 27px;
    margin-top: 10px;
    border: 1px solid #5f5fb3;
    border-radius: 2px;
    background: #474787;
    color: white;
}
#add_node_btn:hover{
    cursor: pointer;
    background: #5f5fb3;
}
#add_data_modal{
    width: 400px;
    height: 420px;
    background: white;
    display:block;
    margin: 0 auto;
    margin-top: 30px;
}
#add_data_modal_container{
    width: 90%;
    display: block;
    margin: 0 auto;
}

/* make a class */
#add_data_folder:nth-child(2){
    margin-top: -10px;
}

#add_data_folder{
    width: 314px;
    height: 30px;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding-left: 10px;
}
#add_data_key{
    width: 300px;
    height: 30px;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding-left: 10px;
    margin-top: 10px;
}
#add_data_value{
    width: 300px;
    height: 100px;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding-left: 10px;
    margin-top: 10px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  resize: none;
}

#add_data_file{
    margin-top: 10px;
}

#add_data_btn{
    width: 100px;
    height: 27px;
    margin-top: 10px;
    border: 1px solid #5f5fb3;
    border-radius: 2px;
    background: #474787;
    color: white;
}
#add_data_btn:hover{
    cursor: pointer;
    background: #5f5fb3;
}

#add_folder_modal{
    width: 400px;
    height: 200px;
    background: white;
    display:block;
    margin: 0 auto;
    margin-top: 50px;
}

#view_data_modal{
    width: 600px;
    padding: 20px;
    background: white;
    display:block;
    margin: 0 auto;
    margin-top: 50px;
}