

#main{
    padding-top: 110px;
    width: 90%;
    display:block;
    margin: 0 auto;
}

#main_title_wrapper{
    height: 70px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #ccc;
}
#main_title{
    font-size: 40px;
    
}
#num_docs{
    margin-left: 10px;
    padding-top: 5px;
}

#add_folder{
    border: 1px solid #5f5fb3;
    background: #474787;
    margin-left: 10px;
    margin-top: 5px;
}

#add_folder:hover{
    cursor: pointer;
    background: #5f5fb3;
}

#data_container{
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-top: 20px;
}

.data{
    width: 30%;
    max-width: 300px;
    background: #e0e0e0;
    border-bottom: 1px solid #ccc; 
}

.data:hover{
    cursor: pointer;
}

.text{
   min-height: 100px;
   max-height: 150px;
}
.data_image{
    width: 70px;
    display: block;
    margin: 0 auto;
}
.data_type{
    text-align: center;
    margin-top: 0;
}
.modal_image{
    width: 100%;
    display: block;
    margin: 0 auto;
}
.data_key{
    background: #2c2c54;
    display: flex;
    align-items: center;
}

.data_key h1{
    font-weight: 500;
    font-size: 16px;
    color: white;
    padding-left: 5px;
}
.data_value{
    font-weight: 400;
    font-size: 16px;
    padding-left: 5px;
}
.filetext{
    white-space: pre-line;
}